.form__title {
  font-family: 'Inter', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 18px;
  line-height: 22px;
  margin: 25px 0 75px;
}

@media screen and (min-width: 1024px) {
  .form__title {
    font-size: 24px;
    line-height: 29px;
    margin: 34px 0 48px;
    align-self: flex-start;
    margin-left: 36px;
  }
}
